const requestBlogs = () => {
  return {
    type: 'REQUEST_BLOGS'
  }
}

const receiveBlogs = (blogs) => {
  debugger;
  return {
    type: 'RECEIVE_BLOGS',
    data: blogs
  }
}

export const setFilter = (name) => {
  return {
    type: 'SET_FILTER',
    author: name
  }
}

export const addBlog = (blog) => {
  return {
    type: 'ADD_BLOG',
    blog
  }
  
  return (dispatch) => {
    return fetch('/blog', {
        method: 'POST', 
        body: JSON.stringify(blog)
      })
      .then(() => fetchBlogs())
      .catch(function(err) {
        console.log('Fetch Error Blogs:', err);
      });  
  }
}

export const fetchBlogs = () => {
  return (dispatch) => {
    dispatch(requestBlogs());
    
    return fetch('/blog')
      .then(response => response.json())
      .then(json => {console.log(json);dispatch(receiveBlogs(json))})
      .catch(function(err) {
        console.log('Fetch Error Blogs:', err);
      });  
  }
}


